import { useTranslation } from 'react-i18next'

export default function LanguagePicker({lang, changeLanguage}: any) {
    const { t, i18n } = useTranslation()
    return (
        <div id="language-switcher">
            <select name="language" id="language" value={lang} onChange={(e) => {localStorage.setItem('language', e.target.value); i18n.changeLanguage(e.target.value); changeLanguage(e.target.value) }}>
                <option value="en">EN</option>
                <option value="de">DE</option>
                <option value="it">IT</option>
                <option value="fr">FR</option>
                <option value="es">ES</option>
            </select>
        </div>
    )
}
  