export default function ItemMenu({data, cart, changeQuantity, activeFilter, search}: any) {
  return (
      
    <div id="active-category">
    {
        data.menu?.map((item: any, key: number) => (
            <div key={key} className={`${activeFilter && activeFilter == item.navigation.id || activeFilter == 0 ? 'active' : 'hidden'} menuItem`}>
                <h1>{item.navigation.name}</h1>
                <h2>{item.title}</h2>
                {
                    item.items?.filter((item: any) => item.name?.includes(search)).map((subitem: any, key: number) => (
                        <div key={key} className="product-row">
                            <div className="product-info">
                                <div className="product-name">{subitem.name}</div>
                                <div className="product-price">{data.currency} {subitem.price}</div>
                                <div className="product-description">{subitem.description}</div>
                                
                            </div>
                            <div className="product-quantity">
                                <div className='quantity'>{cart?.find((c: any) => c?.id == subitem.id)?.quantity}</div>
                                <div className="change-quantity">
                                    <span onClick={() => { changeQuantity(0, item.id, subitem.id) }}>-</span><span onClick={() => { changeQuantity(1, item.id, subitem.id) }}>+</span>
                                </div>
                            </div>
                        </div>
                    ))
                }
            </div>
        ))}
        </div>
  );
}
