const request = async (path: string, lang: string) => {
  const response = await fetch(`https://digiserve.cloudhorizon.com/api/v1/${path}`, {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
      "x-language": "it"
    }
  }
  );

  const resData = await response.json();
  return resData;
}

const send = async (path: string, data: any) => {
  const rawResponse = await fetch(`https://digiserve.cloudhorizon.com/api/v1/${path}`, {
    method: 'POST',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json'
    },
    body: JSON.stringify(data)
  });
  const content = await rawResponse.json();

}

const getMenu = async (location: string | undefined, table: string | undefined, signature: string | undefined, lang: string) => {
  const resData = await request(`products-list/location/${location}/table/${table}?signature=${signature}`, lang);
  return resData;
}


const sendOrder = async (data: any, table: string | number | undefined) => {
  const resData = await send(`submit-order/${table}`, data);

  return resData;
}

export { getMenu, sendOrder };