import { useEffect, useState } from 'react'
import logo from '../../assets/logo.png'
import { Link, useNavigate, useParams } from "react-router-dom"
import { sendOrder } from '../../api/api'
import { useTranslation } from 'react-i18next'
import LanguagePicker from '../../components/languagePicker/languagePicker'

export default function Details() {
    const navigate = useNavigate()
    const [showError, setShowError] = useState(false)
    const [showPaymentError, setShowPaymentError] = useState(false)
    const { location, table, signature } = useParams()
    const [language, setLanguage] = useState(localStorage.getItem('language') || 'en')
    const { t, i18n } = useTranslation()
    const [meta, setMeta] = useState<any>({})
    const [form, setForm] = useState<any>([
        { name: 'room', value: null, },
        { name: 'first_name', value: null, },
        { name: 'last_name', value: null, },
    ])

    const [paymentMethods, setPaymentMethods] = useState<any>(
        [
            { id: 1, value: 'cash', label: t('pay_cash'), selected: false },
            { id: 2, value: 'card', label: t('pay_card'), selected: false },
            { id: 3, value: 'room', label: t('charge_to_room'), selected: false }
        ]
    )


    const changeLanguage = (lang: string) => {
        setLanguage(lang)
    }

    const changePaymentType = (id: number, value: string) => {
        setPaymentMethods(
            paymentMethods.map((method: any) => {
                return {
                    ...method,
                    selected: id == method.id ? !method.selected : false
                }
            })
        )
    }

    const updateForm = (name: string, value: string) => {
        setForm(
            form.map((item: any) => {
                return {
                    ...item,
                    value: item.name == name ? value : item.value
                }
            })
        )
    }

    const validate = async () => {
        let incomplete = form.find((item: any) => item.value == null || item.value == '');
        let selectedMethod = paymentMethods.find((item: any) => item.selected)
        setShowError(false)
        setShowPaymentError(false)

        if (incomplete) {
            setShowError(true)
        }

        if (!selectedMethod) {
            setShowPaymentError(true)
        }

        if (!incomplete && selectedMethod) {
            setShowError(false)
            setShowPaymentError(false)

            let pack: any = {
                order: {
                    products: JSON.parse(localStorage.getItem('cart')!),
                    details: {
                        payment_type: paymentMethods.find((item: any) => item.selected).value,
                        name: form.find((item: any) => item.name == 'first_name').value,
                        surname: form.find((item: any) => item.name == 'last_name').value,
                        room: form.find((item: any) => item.name == 'room').value,
                    }
                }
            }

            const send = await sendOrder(pack, table)
            navigate(`/confirmation/${location}/${table}/${signature}`)
        }
    }

    useEffect(() => {
        setMeta(JSON.parse(localStorage.getItem('metaInfo')!))
    }, [])

    return (
        <div className="details">
            <div className="container">
                <div className="digiserve-logo">
                    <img src={meta.logo ? meta.logo : logo} alt="logo" />
                </div>
                <div className="clear"></div>
                <div id="beach-number">{meta.location} - {t('nr')} {table}</div>
                <LanguagePicker lang={language} changeLanguage={(lang: string) => { changeLanguage(lang)}}/>
                <div className="clear"></div>
            </div>
            <div id="content" className="container">
                <div id="details">
                    <p className={`${showError ? 'error' : 'normal'}`}>{t('room_details')}</p>
                    <input type="text" name="room" value={form.room} placeholder={t('room_number')} onChange={(e) => { updateForm('room', e.target.value) }} />
                    <input type="text" name="first_name" value={form.first_name} id="" className="half-width" placeholder={t('first_name')} onChange={(e) => { updateForm('first_name', e.target.value) }} />
                    <input type="text" name="last_name" id="" value={form.last_name} className="half-width" placeholder={t('last_name')} onChange={(e) => { updateForm('last_name', e.target.value) }} />
                    <div className="clear"></div>
                </div>
                <div id="payment-method">
                    <p className={`${showPaymentError ? 'error' : 'normal'}`}>{t('payment_method')}</p>
                    <div id="payment-buttons">
                        {
                            paymentMethods.map((item: any, key: number) => (
                                <div key={key} onClick={() => changePaymentType(item.id, item.value)} className={`payment-button ${item.selected ? 'selected' : 'regular'}`} >{item.label}</div>
                            ))
                        }

                    </div>
                </div>
                <div id="order-button" className="big-blue" onClick={() => { validate() }}>{t('order')}</div>
                <Link to='/overview'>
                    <div id="return">
                        <a href="#">{t('back')}</a>
                    </div>
                </Link>
            </div>
            <footer className="container">
                <div id="copyright">
                    Powered by DigiServe
        </div>
            </footer>
        </div>
    );
}
