import { useEffect } from 'react'
import logo from '../../assets/logo.png'
import { Link, useNavigate, useParams } from "react-router-dom"
import { useTranslation } from 'react-i18next'
import ItemMenu from '../../components/ItemMenu/ItemMenu'
import { useState } from 'react'
import LanguagePicker from '../../components/languagePicker/languagePicker'

export default function Overview() {
    const [cart, setCart] = useState<any>([])
    const [meta, setMeta] = useState<any>({})
    const [language, setLanguage] = useState(localStorage.getItem('language') || 'en')
    const { t, i18n } = useTranslation()
    let navigate = useNavigate()
    const { location, table, signature } = useParams()

    useEffect(() => {
        setCart(JSON.parse(localStorage.getItem('cart')!))
        setMeta(JSON.parse(localStorage.getItem('metaInfo')!))
    }, [])

    function changeQuantity(operation: any, id: number) {
        let cartArray: any = cart
        let matching = cart.find((item: any) => id == item.id)

        if (operation) {
            cartArray =
                cartArray.map((merch: any) => {
                    let action
                    if (id == merch.id) {
                        action = operation ? 1 : 0
                    }

                    return {
                        ...merch,
                        quantity: matching.id == merch.id ? merch.quantity + 1 : merch.quantity
                    }
                })
        } else {
            if (matching.quantity > 0) {
                cartArray =
                    cartArray.map((item: any) => {
                        return {
                            ...item,
                            quantity: item.id == matching.id ? item.quantity - 1 : item.quantity
                        }
                    })
            }
        }

        setCart(cartArray)
    }

    const changeComment = (val: string, id: number) => {
        setCart(
            cart.map((item: any) => {
                return {
                    ...item,
                    comment: item.id == id ? val : item?.comment
                }
            })
        )
    }

    const save = () => {
        localStorage.setItem("cart", JSON.stringify(cart.filter((c: any) => c.quantity > 0)))
        navigate(`/details/${location}/${table}/${signature}`)
    }

    const goBack = () => {
        navigate(`/menu/${location}/${table}/${signature}`) 
    }

    const countItems = () => {
        let counter: number = 0
        cart?.map((item: any) => {
            counter += item?.quantity
        })

        return counter
    }

    const changeLanguage = (lang: string) => {
        setLanguage(lang)
    }

    return (
        <div>
            <div className="container">
                <div className="digiserve-logo">
                    <img src={meta.logo ? meta.logo : logo} alt="logo" />
                </div>
                <div className="clear"></div>
                <div id="beach-number">{meta.location} - {t('nr')} {table}</div>
                <LanguagePicker lang={language} changeLanguage={(lang: string) => { changeLanguage(lang)}}/>
                <div className="clear"></div>
            </div>
            <div id="content" className="container">
                {
                    cart.map((item: any, key: number) => (
                        <div key={key} className="product-row">
                            <div className="product-info">
                                <div className="product-name">{item.name}</div>
                                <div className="product-price">{meta.currency} {item.price}</div>
                                <div className="product-description">{item.description}</div>
                            </div>
                            <div className="product-quantity">
                                <div className='quantity'>{cart?.find((c: any) => c?.id == item.id)?.quantity}</div>
                                <div className="change-quantity">
                                    <span onClick={() => { changeQuantity(0, item.id) }}>-</span><span onClick={() => { changeQuantity(1, item.id) }}>+</span>
                                </div>
                            </div>

                            <div className="product-comment">
                                <input type="text" value={item?.comment} onChange={(e) => { changeComment(e.target.value, item.id) }} placeholder={t('comment')} />
                            </div>
                        </div>
                    ))
                }

                <Link to='/menu'>
                    <div id="return">
                        <a className="btn" onClick={() => { navigate(`/menu/${location}/${table}/${signature}`) }}>{t('return')}</a>
                    </div>
                </Link>
                <div id="active-category">
                    <div id="total" onClick={() => { save() }}>
                        <div>{t('total_items')}</div>
                        <div id="total-number">{countItems()}</div>
                        <div>{t('basket')}</div>
                    </div>
                    <div id="disclaimer">
                        <p>{t('total_price')}</p>
                        <p>{t('premium')}</p>
                    </div>
                </div>
            </div>
            <footer className="footer">
                <div id="copyright">
                    Powered by DigiServe
        </div>
            </footer>
        </div>
    );
}
