import React, { useState, useEffect, } from 'react'
import homeImg from '../../assets/home.jpeg'
import logo from '../../assets/logo.png'
import { useNavigate, useParams } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { getMenu } from '../../api/api'
import LanguagePicker from '../../components/languagePicker/languagePicker'

export default function Home() {
    const { t, i18n } = useTranslation()
    let navigate = useNavigate()
    const { location, table, signature } = useParams()
    let response: any = []
    const [language, setLanguage] = useState<string>('en')
    const [meta, setMeta] = useState<any>({})

    useEffect(() => {
        getData()

        if (localStorage.getItem('language')) {
            setLanguage(localStorage.getItem('language') || 'en')
            i18n.changeLanguage(localStorage.getItem('language') || 'en')
        } else {
            setLanguage('en');
            localStorage.setItem('language', 'en')
        }
    }, [])

    const changeLanguage = (lang: string) => {
        setLanguage(lang)
    }

    const getData = async () => {
        response = await getMenu(location, table, signature, language)
        const pack = {
            logo: response.logo,
            image: response.image,
            currency: response.currency,
            location: response.location
        }
        setMeta(pack)
        localStorage.setItem("metaInfo", JSON.stringify(pack))
    }

    return (
        <div className="home">
            <div className="wrapper">
                <header className="container">
                    <div id="beach-number">{meta.location} - {t('nr')} {location}</div>
                    <LanguagePicker lang={language} changeLanguage={(lang: string) => { changeLanguage(lang)}}/>
                    <div className="clear"></div>
                </header>
                <div id="content" className="container">
                    <h1>{t('ordering_menu')}</h1>
                    <div id="home-image">
                        <img src={meta.image ? meta.image : homeImg} alt="home" />
                    </div>
                    <div className="digiserve-logo">
                        <img src={meta.logo ? meta.logo : logo} alt="logo" />
                    </div>
                    <div id="menu-btn">
                        <a className="btn" onClick={() => { navigate(`/menu/${location}/${table}/${signature}`) }}>{t('click')}</a>
                    </div>

                </div>
            <footer className="footer">
                <div id="copyright">
                    Powered by DigiServe
        </div>
            </footer>

            </div>
        </div>
    );
}
