import { useState, useEffect } from 'react'
import homeImg from '../../assets/home.jpeg'
import { Link, useNavigate, useParams } from "react-router-dom"
import logo from '../../assets/logo.png'
import { useTranslation } from 'react-i18next'

export default function Confirmation() {
    const [timer, setTimer] = useState(10)
    const { location, table, signature } = useParams()
    const navigate = useNavigate()
    const [language, setLanguage] = useState(localStorage.getItem('language') || 'en')
    const { t, i18n } = useTranslation()
    const [meta, setMeta] = useState<any>({})

    useEffect(() => {
        setMeta(JSON.parse(localStorage.getItem('metaInfo')!))

        const int = setInterval(() => {
            if (timer == 1) {
                navigate(`/${location}/${table}/${signature}`)
            }
            setTimer(timer - 1)
        }, 1000)

        return () => clearInterval(int)
    }, [timer])
  return (
    <div className="confirmation">
      <div id="content" className="container">
          <div id="confirm-image">
              <img src={meta.image? meta.image : homeImg} alt="home" />
          </div>
          <div className="digiserve-logo">
              <img src={meta.logo ? meta.logo : logo} alt="logo" />
          </div>
          <h1>{timer}</h1>
          <div id="confirmation-message">
              <p>{t('thank_you')}</p>
              <p>{t('delivery')} {meta.location} - {t('nr')} {table}</p>
          </div>
      </div>
      <footer className="container">
          <div id="copyright">
              Powered by DigiServe
          </div>
      </footer>
  </div>
  );
}
