import react, {useState} from 'react'

export default function changeQuantity(data: any, operation: any, id: number, subitemId: number, cart: any) {
    const selectedItem = data.menu.find((item: any) => item.id == id)
    const subitem = selectedItem.items.find((item: any) => item.id === subitemId)
    const matching: any = cart.find((item: any) => item.id === subitemId)
    let cartArray: any = cart

    if (matching) {
        if (operation) {
            cartArray = 
                cartArray.map((merch: any) => {
                    let action
                    if (subitemId == merch.id) {
                        action = operation ? 1 : 0
                    }

                    return {
                        ...merch,
                        quantity: matching.id == merch.id ? merch.quantity + 1 : merch.quantity
                    }
                })
        } else {
            if (matching.quantity > 0) {
                cartArray = 
                    cartArray.map((item: any) => {
                        return {
                            ...item,
                            quantity: item.id == matching.id ? item.quantity - 1 : item.quantity
                        }
                    })
            }
        }
    }
    else if (operation) {
        cartArray = [
            ...cart,
            { ...subitem, quantity: 1 }
        ]
    }

    return cartArray
}