import React, { useState, useEffect } from 'react'
import logo from '../../assets/logo.png'
import { useParams, useNavigate } from 'react-router-dom'
import './style.css'
import { useTranslation } from 'react-i18next'
import useChangeQuantity from '../../customHooks/useChangeQuantity'
import ItemMenu from '../../components/ItemMenu/ItemMenu'
import LanguagePicker from '../../components/languagePicker/languagePicker'

import { getMenu } from '../../api/api'

export default function Menu(props: any) {
    const { t, i18n } = useTranslation()
    const [filters, setFilters] = useState<any>([])
    const [data, setData] = useState<any>([])
    const [cart, setCart] = useState<any>([])
    const [search, setSearch] = useState<string>('')
    let navigate = useNavigate()
    const [language, setLanguage] = useState(localStorage.getItem('language') || 'en')
    const [activeFilter, setActiveFilter] = useState<string | number>(0)
    const { location, table, signature } = useParams()
    let response: any = []
    const [meta, setMeta] = useState<any>({})

    function ChangeQuantity (operation: any, id: number, subitemId: number) {
        setCart(useChangeQuantity(data, operation, id, subitemId, cart))
    }

    const saveCart = () => {
        localStorage.setItem("cart", JSON.stringify(cart.filter((c: any) => c.quantity > 0)))
        navigate(`/overview/${location}/${table}/${signature}`)
    }

    useEffect(() => {
        getData()
        setMeta(JSON.parse(localStorage.getItem('metaInfo')!))
    }, [location])

    const changeLanguage = (lang: string) => {
        setLanguage(lang)
    }

    const getData = async () => {
        response = await getMenu(location, table, signature, language)
        setFilters(response.top_navigation)
        setData(response)
        let cartArray: any = []
        response.menu.map((item: any) => {
            item.items.map((subitem: any) => {
                cartArray.push({
                    ...subitem,
                    quantity: 0
                })
            })
        })

        setCart(cartArray)
    }

    const countItems = () => {
        let counter: number = 0
        cart?.map((item: any) => {
            counter += item?.quantity
        })

        return counter
    }

    useTranslation('de')
    return (
        <div>
            <div className="container">
                <div className="digiserve-logo">
                    <img src={meta.logo ? meta.logo : logo} alt="logo" />
                </div>
                <div className="clear"></div>
                <div id="beach-number">{meta.location} - {t('nr')} {table}</div>
                <LanguagePicker lang={language} changeLanguage={(lang: string) => { changeLanguage(lang)}}/>
                <div className="clear"></div>
            </div>
            <div id="content" className="container">
                <div id="search-wrap">
                    <div id="search-button"></div>
                    <input type="text" name="search" id="search" placeholder={t('search')} value={search} onChange={(e) => {setSearch(e.target.value)}} />
                    <div id="voice-search"></div>
                </div>
                <div className="categories">
                    {filters?.map((filter: any, key: number) => (
                        <a key={key} className={filter.id == activeFilter ? 'active' : ''} onClick={() => { setActiveFilter(filter.id == activeFilter ? 0 : filter.id) }}>{filter.name}</a>
                    ))}
                </div>
                <div className="clear"></div>
                {data && <ItemMenu data={data} cart={cart} changeQuantity={(operation: number, id: number, subitemId: number ) => {ChangeQuantity(operation, id, subitemId)}} activeFilter={activeFilter} search={search}></ItemMenu>}
                <div id="active-category">
                    <div id="total" onClick={() => {saveCart()}}>
                        <div>{t('total_items')}</div>
                        <div id="total-number">{countItems()}</div>
                        <div>{t('basket')}</div>
                    </div>
                    <div id="disclaimer">
                        <p>{t('total_price')}</p>
                        <p>{t('premium')}</p>
                    </div>
                </div>
            </div>
            <div>
            </div>
            <footer className="footer">
                <div id="copyright">
                    Powered by DigiServe
        </div>
            </footer>
        </div >
    )
}